import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AuthView from '../views/AuthView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: { title: '智能茶园系统 - 首页' }
  },
  {
    path: '/login',
    name: 'login',
    component: AuthView,
    meta: { title: '智能茶园系统 - 登录' }
  },
  {
    path: '/register',
    name: 'register',
    component: AuthView,
    meta: { title: '智能茶园系统 - 注册' }
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: '/'
  }
]

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes
})

// 设置页面标题
router.beforeEach((to, from, next) => {
  document.title = to.meta.title || '智能茶园系统'
  next()
})

export default router